#seek::-webkit-slider-runnable-track {
  width: 100%;
  border-radius: 4px;
  height: 20px;
  cursor: pointer;
  background: #bbb; }

#seek::-webkit-slider-thumb {
  height: 20px;
  width: 4px;
  border-radius: 2px;
  background: #666666;
  cursor: pointer;
  -webkit-appearance: none; }

#seek:focus::-webkit-slider-runnable-track {
