#seek::-ms-track {
  width: 100%;
  border-radius: 4px;
  height: 20px;
  cursor: pointer;
  background: transparent;
  color: transparent; }

#seek::-ms-fill-lower {
  background: #9c9c9c; }

#seek::-ms-fill-upper {
  background: #bbb; }

#seek::-ms-thumb {
  height: 20px;
  width: 4px;
  border-radius: 2px;
  background: #666666;
  cursor: pointer;
  height: 20px; }

#seek:focus::-ms-fill-lower {
  background: #bbb; }

#seek:focus::-ms-fill-upper {
