.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }
  .loader div {
    display: block;
    position: relative;
    left: 50%;
    top: 50%;
    width: 70px;
    height: 70px;
    margin: -75px 0 0 -75px;
    border-radius: 50%;
    border: 2px solid transparent;
    border-top-color: #6ea6dc;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite; }
    .loader div:before {
      content: "";
      position: absolute;
      top: 5px;
      left: 5px;
      right: 5px;
      bottom: 5px;
      border-radius: 50%;
      border: 2px solid transparent;
      border-top-color: #6e8bdc;
      -webkit-animation: spin 1.1s linear infinite;
              animation: spin 1.1s linear infinite; }
    .loader div:after {
      content: "";
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      border-radius: 50%;
      border: 2px solid transparent;
      border-top-color: #6e6fdc;
      -webkit-animation: spin 0.9s linear infinite;
              animation: spin 0.9s linear infinite; }

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.play-loader-wrapper {
